import Layout from '../components/MyLayout.js'
import React from 'react'
import Link from 'next/link'
import Router, { withRouter } from 'next/router';
import { ACCESS_TOKEN } from '../constants';

export default class Tracks extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tracks: null,
    }
  }


  componentDidMount (props) {
    const access_token = Router.router.query.access_token
    const playlist_id = Router.router.query.playlist_id

    if (access_token !== null) {
      fetch(`https://api.spotify.com/v1/playlists/${playlist_id}/tracks`, {
          headers: {
            'Authorization': 'Bearer ' + access_token
          }
      }).then(res => res.json())
        .then(response => this.setState({tracks: response}))
        .catch(error => console.error('Error:', error));
    } else {
      alert("you are not logged in");
    }


  //  // audio features for up to 100 songs at a TimeRanges. 
  //   "https://api.spotify.com/v1/audio-features/?ids=4JpKVNYnVcJ8tuMKjAj50A,2NRANZE9UCmPAS5XVbXL40,24JygzOLM0EmRQeGtFcIcG"


  }


  render () {
    let playlists = this.state.tracks;
    return  (
          <Layout>
             <p>Tracks</p>
          </Layout>
      )
  }
}
